@import '../../../style/base.scss';

.FullStoryView{

  @include e(header){
    padding: 5px;  
    font-size: 12px;
    width: 96%;

    @include mq(md){
      font-size: 16px;
    }
  }

  @include e(details){
    margin-top: 4px;
    font-size: 13px;
    line-height: 1.6;
    span{
      display: inline-block;
      margin-right: 6px;
    }

    .gray{
      color: $slate-gray;
    }
    @include mq(md){
      font-size: 15px;
    }

  }

  a{
    &:hover{
      color: lighten($color: $black, $amount: 40%)
    }
  }

  @include e(username){
    a{
      color: $blue;
      &:hover{
        color: lighten($color: $blue, $amount: 20%)
      }
    }
  } 

  @include e(loading){
    text-align: center;
    margin-top: 20px;
    font-size: 16px;
  }

  @include e(comment){
    margin-top: 14px;
    padding: 10px 0px;
  }

  @include e(comment-header){
    padding:5px;
  }
}

